const JsyServer = require("../../../services/jsy-server.js");
let that;
let customerPage = 1,
    pageSize = 20,
    recordPage = 1,
    customerYN = true,
    recordYN = true;

export default {
  data() {
    return {
      selectContent: [{
        name: '全部',
        id: 1
      }, {
        name: '未完善',
        id: 0
      }],
      bindSelect: false,
      //是否点开搜素类别
      tabSelection: 1,
      //1选中客户列表；2选中跟进记录
      customerList: [],
      //客户列表
      recordList: [],
      timeIconStatus: false,
      //时间区间选择是否被打开
      startDate: '',
      endDate: '',
      newTime: '',
      inputValue: '',
      //搜索内容
      renewCustomer: false,
      //新建客户后刷新客户列表数据
      renewRecord: false //新建客户后刷新跟进记录列表数据

    };
  },

  onReachBottom() {
    if (that.tabSelection == 1 && customerPage > -1) {
      this.getCustomerList();
    }

    if (that.tabSelection == 2 && recordPage > -1) {
      this.getRecordList();
    }
  },

  onPullDownRefresh() {
    customerPage = 1, recordPage = 1, this.getCustomerList();
    this.getRecordList();
  },

  onShow: function () {
    if (this.renewCustomer) {
      customerPage = 1;
      this.getCustomerList();
      this.setData({
        renewCustomer: false
      });
    }

    if (this.renewRecord) {
      recordPage = 1;
      this.getRecordList();
      this.setData({
        renewRecord: false
      });
    }
  },
  onLoad: function (options) {
    customerPage = 1, recordPage = 1;
    that = this;
    this.setTime();
    this.getCustomerList();
    this.getRecordList();
  },
  components: {},
  props: {},
  methods: {
    //设置当前时间
    setTime: function () {
      let newDate = new Date();
      let month = newDate.getMonth() + 1;
      month = month < 10 ? "0" + month : month;
      let newTime = newDate.getFullYear() + '-' + month + '-' + newDate.getDate();
      this.setData({
        newTime: newTime
      });
    },
    //获取跟进记录列表
    getRecordList: function () {
      if (recordPage < 0 || !recordYN) {
        return;
      }

      recordYN = false;
      let data = {
        name: this.inputValue,
        pageNo: recordPage,
        pageSize: pageSize,
        startDate: this.startDate,
        endDate: this.endDate
      };
      JsyServer.getRecordList(data).then(res => {
        wx.stopPullDownRefresh();
        recordYN = true;
        res.data.map(item => {
          let olddata = item.followTime;
          let olddata2 = olddata.replace(/-/g, '/');
          let time = new Date(olddata2);
          let weekList = ["星期天", "星期一", "星期二", "星期三", "星期四", "星期五", "星期六"];
          let weekIndex = time.getDay();
          let week = item.followTime.slice(0, 10) + " " + weekList[weekIndex];
          item.timeName = week;
          return item;
        });

        if (recordPage == 1) {
          that.setData({
            recordList: res.data
          });
        } else if (res.data.length > 0) {
          that.setData({
            recordList: that.recordList.concat(res.data)
          });
        }

        if (res.data.length == 20) {
          recordPage++;
        } else {
          recordPage = -1;
        }
      }).catch(err => {
        customerYN = true;
        console.log("err--", err);
      });
    },
    // 获取客户列表
    getCustomerList: function () {
      if (customerPage < 0 || !customerYN) {
        return;
      }

      customerYN = false;
      let data = {
        pageNo: customerPage,
        pageSize: pageSize,
        name: this.inputValue
      };

      if (this.selectContent[0].id == 0) {
        data.status = 0;
      }

      JsyServer.getCustomerList(data).then(res => {
        wx.stopPullDownRefresh();
        customerYN = true;

        if (customerPage == 1) {
          that.setData({
            customerList: res.data
          });
        } else if (res.data.length > 0) {
          that.setData({
            customerList: that.customerList.concat(res.data)
          });
        }

        if (res.data.length == 20) {
          customerPage++;
        } else {
          customerPage = -1;
        }
      }).catch(err => {
        customerYN = true;
        console.log("err--", err);
      });
    },
    // 点击选项框
    bindSelect: function () {
      this.setData({
        bindSelect: !this.bindSelect
      });
    },
    // 点击选项框中的选项(客户列表)
    bindSelectContent: function (e) {
      let index = e.currentTarget.dataset.index;
      let selectContent = this.selectContent;
      let obj = selectContent[0];
      selectContent[0] = selectContent[index];
      selectContent[index] = obj;
      this.setData({
        selectContent: selectContent
      });

      if (index > 0) {
        customerPage = 1;
        this.getCustomerList();
      }
    },
    // 点击搜索
    bindSearch: function (e) {
      this.setData({
        inputValue: e.detail.value
      });

      if (this.tabSelection == 1) {
        customerPage = 1;
        this.getCustomerList();
      } else {
        this.getRecordList();
      }
    },
    inputSearch: function (e) {
      // this.setData({
      // 	inputValue: e.detail.value
      // })
      console.log(e.detail.value);
    },
    // 点击跟进记录中的日期图标
    iconTime: function () {
      this.setData({
        timeIconStatus: !this.timeIconStatus
      });
    },
    // 点击开始时间
    startDateChange: function (e) {
      this.setData({
        startDate: e.detail.value
      });
    },
    // 点击结束时间
    endDateChange: function (e) {
      this.setData({
        endDate: e.detail.value
      });
      recordPage = 1;
      this.getRecordList();
    },
    bindSearchFollow: function () {
      recordPage = 1;
      this.getRecordList();
    },
    //点击tab栏（客户列表，跟进记录） 
    bindTab: function (e) {
      let index = e.currentTarget.dataset.index;
      let tabSelection = this.tabSelection;

      if (index != tabSelection) {
        this.setData({
          tabSelection: index
        });
      }
    },
    //跳转到客户详情
    toCustimerDetails: function (e) {
      let id = e.currentTarget.dataset.id;
      wx.navigateTo({
        url: '/pages/jin-suo-yun/customer-admin/customer-details?id=' + id
      });
    },
    //跳转到跟进记录详情
    toRecordDetalis: function (e) {
      let id = e.currentTarget.dataset.id;
      wx.navigateTo({
        url: '/pages/jin-suo-yun/customer-admin/record-details-one?id=' + id + '&type=true'
      });
    },
    //跳转到新建客户页面
    goCustomerCreated: function () {
      wx.navigateTo({
        url: '/pages/jin-suo-yun/customer-admin/customer-created'
      });
    },
    //跳转到新增跟进记录页
    toAddRecord: function () {
      wx.navigateTo({
        url: '/pages/jin-suo-yun/customer-admin/add-record'
      });
    },
    // 重置时间
    bindReset: function () {
      this.setData({
        startDate: "",
        endDate: ""
      });
    },
    setData: function (obj) {
      let that = this;
      let keys = [];
      let val, data;
      Object.keys(obj).forEach(function (key) {
        keys = key.split('.');
        val = obj[key];
        data = that.$data;
        keys.forEach(function (key2, index) {
          if (index + 1 == keys.length) {
            that.$set(data, key2, val);
          } else {
            if (!data[key2]) {
              that.$set(data, key2, {});
            }
          }

          data = data[key2];
        });
      });
    }
  }
};
